.room-reservation {
  box-sizing: border-box;
  width: rem(380px);
  padding: rem(39px) rem(29px) rem(29px);
  background: $default-color;
  border-radius: $border-radius;
  border: $thin-border;
  box-shadow: $bold-shadow;
  font-family: $base-font;

/* header */
  &__header {
    display: flex;
    justify-content: space-between;
  }

  &__number {
    display: flex;
    justify-content: flex-start;
    align-items: baseline;
  }

  &__title {
    @include title;

    &_smaller {
      @include font-rem(14, 17, bold);
    }
  }

  &__class {
    @include small-subtitle;
    color: $primary-color;
    margin-left: rem(6px);
  }

  &__price {
    @include font-rem(14, 24, bold);
    color: $dark-color-op50;
    display: flex;
    align-items: flex-end;
  }

  &__price-descr {
    font-weight: normal;
    font-size: rem(12px);
  }

/* dates */
  &__dates {
    margin-top: rem(16px);
  }

/* guests */
  &__guests {
    margin-top: rem(16px);
  }

/* bill */
  &__bill {
    padding-top: rem(22px);
    @include font-rem(14, 18, normal);
  }

  &__bill-item {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: rem(16px);
  }

  &__bill-descr {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 75%;
  }

  &__info {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    width: rem(20px);
    height: rem(20px);
    border-radius: 50%;
    border: $light-border;
    color: $dark-color-op25;
    font-size: rem(12px);
    font-weight: bold;
    line-height: 1;
    user-select: none;
    cursor: pointer;
  }

  &__total {
    @include subtitle;
    margin-top: rem(30px);
    display: flex;
    align-items: baseline;
    justify-content: space-between;

    &:after {
      content: '';
      display: block;
      border-bottom: 1px dashed rgba(31, 32, 65, 0.25);
      flex: 1;
      flex-grow: 1;
      order: 2;
      margin: 0 rem(5px);
    }
  }

  &__total-descr {
    order: 1;
  }

  &__total-sum {
    order: 3;
  }

  &__button{
    margin-top: rem(20px);
  }
}